@import url(https://fonts.googleapis.com/css2?family=Dancing+Script&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


header {
    height: 80px;
    display: flex;
    justify-content: space-between;
    flex-wrap: nowrap;
    width: 80%;
    box-shadow: 0px 3px 3px #ccc;
    margin: auto;
}

.header_one, .header_two, .header_three {
    display: flex;
    justify-content: flex-start;
    flex-wrap: nowrap;
    margin-left: 75px;
}
/* First header */

.header_one a img {
    width: 50px;
    height: 50px;
    margin-top: 15px;
}

/* Second header */

.header_two {
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.header_two a {
    padding: 10px 15px;
    text-decoration: none;
    font-size: 24px;
    font-family: 'Dancing Script', cursive;
    color: black;
}
.header_two a:hover {
    color: #ccc;
    transition: 0.6s;
}

/* Third header */ 
.header_three {
    margin-right: 100px;
}
.header_three ul {
    padding: 0;
    display: flex;
    justify-content: flex-start;
    flex-wrap: nowrap;
}
.header_three ul li {
    list-style-type: none;
    margin-right: 12px;
}

.header_three ul li a {
    text-decoration: none;
    color: #000;
}

.header_three ul li a:hover {
    color: #ccc;
    transition: 0.6s;
}
main {
    width: 80%;
    margin: auto;
}
.about {
    padding: 10px 0;
}
h3 {
    text-align: center;
}

p {
    width: 95%;
    margin: auto;
}
h2 {
    text-align: center;
}
.services {
    border: 1px solid black;
}
.image-container {
    display: flex;
    justify-content: center;
}

footer {
    width: 80%;
    margin: auto; 
    height: 80px;
    display: flex;
    justify-content: space-between;
    flex-wrap: nowrap;   
    box-shadow: 0 -3px 3px #ccc;
}
.creator {
    padding: 10px 15px;
}

.creator a img{
    width: 40px;
    height: 40px;
}
